@font-face {
  font-family: InstrumentSerif;
  src: url('./Assets/fonts/InstrumentSerif-Regular.ttf');
}

@font-face {
  font-family: RobotoCondensed;
  src: url('./Assets/fonts/RobotoCondensed-Medium.ttf');
  font-weight: 500;
}

@font-face {
  font-family: RobotoCondensed;
  src: url('./Assets/fonts/RobotoCondensed-Regular.ttf');
  font-weight: 400;
}

:root {
  --gap-size: 20px;
}

.App {
  color: white;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  box-sizing: border-box;
  padding: var(--gap-size);
  display: grid;
  grid-template-columns: repeat(12, [col-start] 1fr);
  gap: var(--gap-size);
  font-family: InstrumentSerif;
  text-transform: uppercase;
}

#backgroundVideo {
  position: fixed;
  right: 0;
  bottom: 0;
  object-fit: cover;
  width: 100%;
  height: 100%;
  z-index: 1;
}

#about {
  z-index: 2;
  mix-blend-mode: overlay;
  margin: 0;
  grid-column-start: 9;
  grid-column-end: 13;
  font-size: 18px;
  text-shadow: 0px 0px 3px #fff;
  line-height: 24px;
}

.titleFont {
  font-family: RobotoCondensed;
  font-size: 20px;
  font-weight: 500;
}

.textSpacing {
  letter-spacing: -2px;
}

#nav {
  grid-column-start: 1;
  grid-column-end: 7;
  z-index: 2;
  mix-blend-mode: overlay;
}

#contact {
  margin: 0;
  color: white;
  font-size: 18px;
  text-shadow: 0px 0px 0px #fff;
  text-decoration: underline;
  height: auto;
  transition: .3s;
}

#contact:hover {
  text-shadow: 0px 0px 4px #fff;
}

@media screen and (max-width: 600px) {
  #backgroundVideo {
    object-position: 20% center;
  }

  #about {
    grid-column-start: 4;
    grid-column-end: 13;
    font-size: 24px;
  }

  .titleFont {
    font-size: 26px;
  }
}